import React from "react";
import "../styles/Nav.css";
import longLogo from "../assets/Logo.png";
import { FaBars } from "react-icons/fa";
import { GrFormClose } from "react-icons/gr";

import "../index.js";

function Nav() {
  let menuOpen = false;

  function toggleMenu() {
    let menuLinks = document.querySelector(".links__menu");

    menuOpen = !menuOpen;

    if (menuOpen === false) {
      menuLinks.style.display = "none";
    } else {
      menuLinks.style.display = "block";
    }
  }

  return (
    <>
      <div className="links__menu">
        <button className="close__links-menu--btn" onClick={toggleMenu}>
          <GrFormClose className="close__links--icon" />
        </button>
        <ul className="menu__links">
          <a onClick={toggleMenu} className="menu__link">STORE</a>
          <a onClick={toggleMenu} className="menu__link">VIDEOS</a>
          <a onClick={toggleMenu} className="menu__link">CONTACT</a>
        </ul>
      </div>
      <nav>
        <div className="nav--container">
          <a href="/" className="header__logo--wrapper">
            <img src={longLogo} className="header__logo" alt="" />
          </a>
          <ul className="header__links">
            <li className="header__link">STORE</li>
            <li className="header__link">VIDEOS</li>
            <li className="header__link">CONTACT</li>
            <button className="header__burger--btn" onClick={toggleMenu}>
              <FaBars className="header__burger-bar" />
            </button>
          </ul>
        </div>
      </nav>
    </>
  );
}

export default Nav;
